nav.layout-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0.25em 2em 0.25em 3em;
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  width: 100%;
  background: rgb(245, 245, 245);
  z-index: 99;
}
nav.layout-navigation ul.list {
  list-style: none;
  display: flex;
  justify-content: none;
  align-items: center;
}
nav.layout-navigation ul.list li {
  margin-left: 2em;
  font-weight: 500;
  font-size: clamp(0.75rem, 2.5vw, 0.9rem);
  transition: 0.3s;
  cursor: pointer;
  position: relative;
  text-align: center;
}
nav.layout-navigation ul.list li:not(.register):not(.login):not(.logout):not(.plan):before {
  content: "";
  width: 0;
  height: 2px;
  position: absolute;
  left: 50%;
  top: 100%;
  bottom: auto;
  right: auto;
  transform: translateX(-50%);
  background: royalblue;
  transition: 0.3s;
}
nav.layout-navigation ul.list li:not(.register):not(.login):not(.logout):not(.plan):hover {
  color: royalblue;
}
nav.layout-navigation ul.list li:not(.register):not(.login):not(.logout):not(.plan):hover:before {
  width: 100%;
}
nav.layout-navigation ul.list li.logout, nav.layout-navigation ul.list li.register {
  border-radius: 1em;
  padding: 0.5em 1em;
  color: rgb(245, 245, 245);
  margin-left: 3em;
}
nav.layout-navigation ul.list li.plan {
  border-radius: 1em;
  padding: 0.5em 1em;
  color: rgb(245, 245, 245);
  margin-left: 1em;
  background: royalblue;
}
nav.layout-navigation ul.list li.plan:hover {
  background: cornflowerblue;
}
nav.layout-navigation ul.list li.logout {
  background: crimson;
}
nav.layout-navigation ul.list li.logout:hover {
  background: red;
}
nav.layout-navigation ul.list li.register {
  background: royalblue;
}
nav.layout-navigation ul.list li.register:hover {
  background: rgb(229, 62, 1);
}
nav.layout-navigation ul.list li.login {
  font-weight: 700;
  margin-left: 1em;
}
nav.layout-navigation ul.list li.login:hover {
  color: royalblue;
}
nav.layout-navigation figure.logo {
  height: 100%;
  overflow: hidden;
}
nav.layout-navigation figure.logo img {
  height: 100%;
}
@media (max-width: 768px) {
  nav.layout-navigation {
    justify-content: flex-end;
  }
  nav.layout-navigation figure.logo {
    display: none;
  }
}
@media (max-width: 480px) {
  nav.layout-navigation {
    padding: 0.25em 0.5em 0.25em 0.5em;
  }
  nav.layout-navigation ul.list li {
    margin-left: 0;
  }
  nav.layout-navigation ul.list li.logout, nav.layout-navigation ul.list li.register {
    margin-left: 1.5em;
  }
  nav.layout-navigation ul.list li.plan {
    border-radius: 1em;
    width: max-content;
    margin-left: 1em;
  }
  nav.layout-navigation ul.list li:nth-child(2), nav.layout-navigation ul.list li:nth-child(3) {
    display: none;
  }
}