section.advantages {
  min-height: 100vh;
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
section.advantages section.cards {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 6em;
  width: 100%;
  flex-wrap: wrap;
  align-items: stretch;
}
section.advantages section.cards article.card {
  background: whitesmoke;
  padding: 1em;
  border-radius: 0.25em;
  width: 330px;
  margin: 0.25em;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}
section.advantages section.cards article.card::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-50%, -50%);
  display: block;
  height: 50px;
  width: 50px;
  border: 3px solid royalblue;
  border-radius: 50%;
}
section.advantages section.cards article.card h3 {
  font-weight: 800;
  font-size: 1.1rem;
  margin-top: 2em;
  margin-bottom: 0.75em;
}
section.advantages section.cards article.card p {
  margin-bottom: 2.5em;
  font-size: clamp(0.9rem, 4vw, 1rem);
  font-weight: 700;
  font-family: "Comfortaa", cursive;
}
section.advantages section.cards article.card span.icon {
  display: block;
  height: clamp(50px, 15vw, 80px);
  width: clamp(50px, 15vw, 80px);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
section.advantages section.cards article.card span.icon:not(.plus) {
  border: 2px solid royalblue;
}
section.advantages section.cards article.card span.icon svg {
  width: 70%;
  height: 70%;
}
section.advantages div.text {
  text-align: center;
}
section.advantages div.text h2 {
  font-size: 1rem;
  font-weight: 600;
}
section.advantages div.text h1 {
  font-size: clamp(2rem, 6vw, 3rem);
  font-weight: 200;
  color: rgb(83, 83, 83);
}
@media (max-width: 480px) {
  section.advantages {
    padding: 0.25em;
  }
  section.advantages section.cards {
    margin-top: 2em;
  }
}